<template>
  <div id="app">
    <PortifolioHeader @change-component="changeComponent" />
    <PortifolioSection :current-component="currentcomponent"/>
    <SectionFooter/>
  </div>
</template>

<script>
 
import PortifolioSection from './components/PortifolioSection.vue'
import PortifolioHeader from './components/PortifolioHeader.vue'
import SectionFooter from './components/SectionFooter.vue'
export default {
  name: 'App',
  components: {
    PortifolioHeader,
    PortifolioSection,
    SectionFooter
  },
  data(){
    return {
      currentcomponent: 'SectionHome'
    }
  },
  methods:{
    changeComponent(value){
      let component;
      switch(value){
        case 'home':
          component = 'SectionHome';
          break;
        case 'sobre' :
          component= 'SectionSobre';
          break;
        case 'projeto':
          component = 'SectionProjetos';
          break;
      }
      this.currentcomponent = component;
    }
  }
}
</script>

